import React, { useState, useRef, useEffect } from 'react';
import styled, { keyframes } from 'styled-components';
import { motion } from 'framer-motion';

const blink = keyframes`
  0%, 100% { opacity: 1; }
  50% { opacity: 0; }
`;

const Container = styled(motion.section)`
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 2rem;
  position: relative;
  overflow: hidden;
`;

const Terminal = styled.div`
  width: 100%;
  max-width: 800px;
  background: rgba(0, 0, 0, 0.8);
  border: 2px solid #00ff00;
  border-radius: 8px;
  padding: 2rem;
  font-family: 'Press Start 2P', monospace;
  color: #00ff00;
  box-shadow: 0 0 20px rgba(0, 255, 0, 0.3);
  position: relative;
  overflow: hidden;

  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: repeating-linear-gradient(
      0deg,
      rgba(0, 255, 0, 0.03) 0px,
      rgba(0, 255, 0, 0.03) 1px,
      transparent 1px,
      transparent 2px
    );
    pointer-events: none;
  }
`;

const Title = styled.h2`
  font-size: 1.5rem;
  margin-bottom: 2rem;
  text-align: center;
  text-shadow: 0 0 10px #00ff00;
`;

const CommandLine = styled.div`
  display: flex;
  align-items: center;
  gap: 0.5rem;
  margin-bottom: 1rem;
`;

const Prompt = styled.span`
  color: #00ff00;
  margin-right: 0.5rem;
`;

const Input = styled.input`
  background: transparent;
  border: none;
  color: #00ff00;
  font-family: 'Press Start 2P', monospace;
  font-size: 1rem;
  flex: 1;
  outline: none;

  &::placeholder {
    color: rgba(0, 255, 0, 0.5);
  }
`;

const Output = styled.div`
  margin-bottom: 1rem;
  white-space: pre-wrap;
  word-wrap: break-word;
`;

const Cursor = styled.span`
  display: inline-block;
  width: 2px;
  height: 1em;
  background: #00ff00;
  margin-left: 0.5rem;
  animation: ${blink} 1s infinite;
`;

const HelpText = styled.div`
  color: #00ffff;
  margin-bottom: 1rem;
  text-shadow: 0 0 10px #00ffff;
`;

const commands = {
  help: `Available commands:
  help - Show this help message
  about - Learn more about me
  projects - View my projects
  contact - Get my contact information
  clear - Clear the terminal
  exit - Close the terminal`,
  about: `I'm a passionate software developer with expertise in web development and game development.
  I love creating interactive experiences and solving complex problems.`,
  projects: `Here are some of my projects:
  1. Web Portfolio - A retro-styled portfolio website
  2. Pixel Art Generator - Create pixel art from images
  3. Retro Game Collection - A collection of classic games
  4. Terminal Emulator - A web-based terminal simulator`,
  contact: `You can reach me at:
  Email: your.email@example.com
  GitHub: github.com/yourusername
  LinkedIn: linkedin.com/in/yourusername
  Twitter: @yourusername`,
  clear: '',
  exit: ''
};

const TerminalMenu: React.FC = () => {
  const [input, setInput] = useState('');
  const [history, setHistory] = useState<Array<{ command: string; output: string }>>([
    { command: 'help', output: commands.help }
  ]);
  const inputRef = useRef<HTMLInputElement>(null);

  useEffect(() => {
    inputRef.current?.focus();
  }, []);

  const handleCommand = (command: string) => {
    const output = commands[command as keyof typeof commands] || 'Command not found. Type "help" for available commands.';
    setHistory(prev => [...prev, { command, output }]);
    setInput('');

    if (command === 'exit') {
      // Handle exit command
      console.log('Exiting terminal...');
    }
  };

  const handleKeyPress = (e: React.KeyboardEvent) => {
    if (e.key === 'Enter') {
      handleCommand(input.toLowerCase());
    } else if (e.key === 'ArrowUp') {
      // Handle command history
      const lastCommand = history[history.length - 1]?.command;
      if (lastCommand) {
        setInput(lastCommand);
      }
    }
  };

  return (
    <Container
      initial={{ opacity: 0, y: 20 }}
      animate={{ opacity: 1, y: 0 }}
      transition={{ duration: 0.5 }}
    >
      <Terminal>
        <Title>Terminal Menu</Title>
        {history.map((item, index) => (
          <div key={index}>
            <CommandLine>
              <Prompt>$</Prompt>
              <span>{item.command}</span>
            </CommandLine>
            {item.output && (
              <Output>
                {item.output}
              </Output>
            )}
          </div>
        ))}
        <CommandLine>
          <Prompt>$</Prompt>
          <Input
            ref={inputRef}
            type="text"
            value={input}
            onChange={(e) => setInput(e.target.value)}
            onKeyPress={handleKeyPress}
            placeholder="Type a command..."
            autoFocus
          />
          <Cursor />
        </CommandLine>
        <HelpText>
          Type "help" for available commands
        </HelpText>
      </Terminal>
    </Container>
  );
};

export default TerminalMenu; 