import React, { useState, useEffect, useRef } from 'react';
import styled, { keyframes } from 'styled-components';
import { motion } from 'framer-motion';

const blink = keyframes`
  0%, 100% { opacity: 1; }
  50% { opacity: 0; }
`;

const bounce = keyframes`
  0%, 100% { transform: translateY(0); }
  50% { transform: translateY(-10px); }
`;

const wag = keyframes`
  0%, 100% { transform: rotate(0deg); }
  50% { transform: rotate(5deg); }
`;

const typewriter = keyframes`
  from { width: 0; }
  to { width: 100%; }
`;

const Container = styled.div`
  position: fixed;
  bottom: 20px;
  left: 20px;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 10px;
`;

const RexContainer = styled(motion.div)`
  position: relative;
  width: 80px;
  height: 80px;
  cursor: pointer;
  background: #00ff00;
  border: 2px solid #00ff00;
  border-radius: 8px;
  padding: 8px;
  box-shadow: 0 0 20px rgba(0, 255, 0, 0.5);
`;

const RexSprite = styled.div`
  width: 100%;
  height: 100%;
  background: url('/rex-sprite.svg') no-repeat center;
  background-size: contain;
  animation: ${bounce} 2s ease-in-out infinite;
  filter: brightness(0) saturate(100%) invert(100%) sepia(0%) saturate(0%) hue-rotate(0deg) brightness(100%) contrast(100%);
`;

const Eye = styled.div`
  position: absolute;
  top: 25px;
  right: 20px;
  width: 6px;
  height: 6px;
  background: #000;
  border-radius: 50%;
  animation: ${blink} 3s infinite;
`;

const Tail = styled.div`
  position: absolute;
  bottom: 15px;
  left: 15px;
  width: 25px;
  height: 10px;
  background: #000;
  transform-origin: left center;
  animation: ${wag} 1s ease-in-out infinite;
`;

const ChatBubble = styled(motion.div)`
  background: rgba(0, 0, 0, 0.8);
  border: 2px solid #00ff00;
  border-radius: 8px;
  padding: 15px;
  max-width: 350px;
  font-family: 'Press Start 2P', cursive;
  font-size: 0.8rem;
  color: #00ff00;
  position: relative;
  margin-bottom: 10px;
  overflow: hidden;
  box-shadow: 0 0 20px rgba(0, 255, 0, 0.2);
  max-height: 400px;
  overflow-y: auto;

  &::-webkit-scrollbar {
    width: 8px;
  }

  &::-webkit-scrollbar-track {
    background: rgba(0, 255, 0, 0.1);
    border-radius: 4px;
  }

  &::-webkit-scrollbar-thumb {
    background: #00ff00;
    border-radius: 4px;
  }
`;

const MessageText = styled.div`
  white-space: pre-wrap;
  word-wrap: break-word;
  overflow: hidden;
  animation: ${typewriter} 0.05s steps(40, end);
  margin-bottom: 8px;
  line-height: 1.4;
`;

const InputContainer = styled.div`
  display: flex;
  gap: 8px;
  width: 100%;
  margin-top: 10px;
`;

const ChatInput = styled.input`
  background: rgba(0, 0, 0, 0.8);
  border: 2px solid #00ff00;
  border-radius: 4px;
  padding: 8px;
  color: #00ff00;
  font-family: 'Press Start 2P', cursive;
  font-size: 0.7rem;
  width: 280px;
  outline: none;

  &::placeholder {
    color: rgba(0, 255, 0, 0.5);
  }
`;

const SendButton = styled.button`
  background: #00ff00;
  border: none;
  border-radius: 4px;
  padding: 8px 16px;
  color: #000;
  font-family: 'Press Start 2P', cursive;
  font-size: 0.7rem;
  cursor: pointer;
  transition: all 0.3s ease;

  &:hover {
    background: #00cc00;
    transform: scale(1.05);
  }
`;

const idleMessages = [
  "Need help?",
  "Waiting for a command...",
  "Dinos love coding too!",
  "Rawr! I'm here to help!",
  "Want to know more about Leonardo?",
  "Check out the projects!",
  "Type 'help' for commands!",
  "Hi there! ",
  "Let's code together!",
  "Need a coding buddy?"
];

const greetings = ['hi', 'hello', 'hey', 'greetings', 'yo', 'sup'];
const farewells = ['bye', 'goodbye', 'see you', 'later', 'cya'];
const thanks = ['thanks', 'thank you', 'thx', 'appreciate it'];
const questions = ['how are you', 'what\'s up', 'what are you', 'who are you'];

const responses = {
  greetings: [
    "Hi there! ",
    "Hello! How can I help you today?",
    "Hey! Ready to code?",
    "Greetings, fellow developer!",
    "Yo! Let's build something awesome!"
  ],
  farewells: [
    "See you later! ",
    "Goodbye! Come back soon!",
    "Bye! Don't forget to commit your changes!",
    "Later! Keep coding!",
    "Cya! Remember to push your code!"
  ],
  thanks: [
    "You're welcome!",
    "No problem!",
    "Anytime!",
    "Glad I could help!",
    "That's what I'm here for!"
  ],
  questions: [
    "I'm Rex, your friendly coding companion! ",
    "I'm doing great! How about you?",
    "I'm a T-Rex who loves coding and helping developers!",
    "I'm your personal coding assistant, here to help!"
  ]
};

const commands = {
  "who are you": "I'm Rex! Your friendly 8-bit guide! ",
  "tell me about leonardo": "Leonardo is a tech wizard! He's passionate about technology and representing Suriname on the global tech stage. Want to see his projects?",
  "show projects": "Here you go! Check out Leonardo's awesome projects in the Projects section!",
  "give me a fun fact": "Did you know T-Rex had tiny arms but a huge brain? Just like how Leonardo's code is compact but powerful!",
  "rawr": "Rawr! That means hello in dino-speak! ",
  "help": `Available commands:
  who are you
  tell me about leonardo
  show projects
  give me a fun fact
  rawr
  help`,
  "default": "Rawr? I didn't understand that. Try typing 'help' for available commands!"
};

const PixelRex: React.FC = () => {
  const [isOpen, setIsOpen] = useState(false);
  const [messages, setMessages] = useState<Array<{ text: string; isUser: boolean }>>([
    { text: "Rawr! I'm Rex, your friendly guide! Type 'help' for commands!", isUser: false }
  ]);
  const [input, setInput] = useState('');
  const [currentIdleMessage, setCurrentIdleMessage] = useState('');
  const messagesEndRef = useRef<HTMLDivElement>(null);

  const scrollToBottom = () => {
    messagesEndRef.current?.scrollIntoView({ behavior: "smooth" });
  };

  useEffect(() => {
    scrollToBottom();
  }, [messages]);

  useEffect(() => {
    if (!isOpen) {
      const interval = setInterval(() => {
        setCurrentIdleMessage(idleMessages[Math.floor(Math.random() * idleMessages.length)]);
      }, 5000);
      return () => clearInterval(interval);
    }
  }, [isOpen]);

  const getResponse = (message: string): string => {
    const lowerMessage = message.toLowerCase().trim();

    // Check for greetings
    if (greetings.some(greeting => lowerMessage.includes(greeting))) {
      return responses.greetings[Math.floor(Math.random() * responses.greetings.length)];
    }

    // Check for farewells
    if (farewells.some(farewell => lowerMessage.includes(farewell))) {
      return responses.farewells[Math.floor(Math.random() * responses.farewells.length)];
    }

    // Check for thanks
    if (thanks.some(thank => lowerMessage.includes(thank))) {
      return responses.thanks[Math.floor(Math.random() * responses.thanks.length)];
    }

    // Check for questions
    if (questions.some(question => lowerMessage.includes(question))) {
      return responses.questions[Math.floor(Math.random() * responses.questions.length)];
    }

    // Check for predefined commands
    if (commands[lowerMessage as keyof typeof commands]) {
      return commands[lowerMessage as keyof typeof commands];
    }

    // Default response with some variation
    const defaultResponses = [
      "Rawr? I'm not sure about that. Try asking something else!",
      "Hmm, I don't understand that. Type 'help' for available commands!",
      "That's beyond my dino knowledge! Try a different question.",
      "I'm a simple T-Rex! Try asking something else or type 'help'!",
      "Rawr! I'm not sure about that one. Need help with something else?"
    ];
    return defaultResponses[Math.floor(Math.random() * defaultResponses.length)];
  };

  const handleSend = () => {
    if (!input.trim()) return;

    const userMessage = input.trim();
    setMessages(prev => [...prev, { text: userMessage, isUser: true }]);

    const response = getResponse(userMessage);
    setTimeout(() => {
      setMessages(prev => [...prev, { text: response, isUser: false }]);
    }, 500);

    setInput('');
  };

  const handleKeyPress = (e: React.KeyboardEvent) => {
    if (e.key === 'Enter') {
      handleSend();
    }
  };

  return (
    <Container>
      <RexContainer onClick={() => setIsOpen(!isOpen)}>
        <RexSprite />
        <Eye />
        <Tail />
      </RexContainer>
      {!isOpen && currentIdleMessage && (
        <ChatBubble
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          exit={{ opacity: 0, y: 20 }}
        >
          <MessageText>{currentIdleMessage}</MessageText>
        </ChatBubble>
      )}
      {isOpen && (
        <ChatBubble
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          exit={{ opacity: 0, y: 20 }}
        >
          {messages.map((message, index) => (
            <MessageText key={index} style={{ color: message.isUser ? '#ffffff' : '#00ff00' }}>
              {message.isUser ? '> ' : ''}{message.text}
            </MessageText>
          ))}
          <div ref={messagesEndRef} />
          <InputContainer>
            <ChatInput
              type="text"
              value={input}
              onChange={(e) => setInput(e.target.value)}
              onKeyPress={handleKeyPress}
              placeholder="Type a message..."
              autoFocus
            />
            <SendButton onClick={handleSend}>Send</SendButton>
          </InputContainer>
        </ChatBubble>
      )}
    </Container>
  );
};

export default PixelRex; 